<style scoped>

</style>
<template>
  <div>
    <Divider orientation="left">列表筛选</Divider>
    <Row class="search">
      <Form inline :model="formData" :label-width="80">
        <FormItem label="资讯标题">
          <Input v-model="formData.title" placeholder="请输入资讯标题" class="filter-input"></Input>
        </FormItem>
        <FormItem label="资讯内容">
          <Input v-model="formData.content" placeholder="请输入资讯内容" class="filter-input"></Input>
        </FormItem>
      </Form>
    </Row>
    <div class="filter-btn">
      <span class="span">操作 </span>
      <Button style="font-weight: bold" type="info" icon="ios-search" @click="loadData(1)"> 搜索</Button>
      <Button style="font-weight: bold" type="warning" icon="md-refresh" @click="ref()">重置</Button>
      <Button style="font-weight: bold" type="success" icon="md-add" @click="add()">新建</Button>
    </div>
    <Divider orientation="left">列表数据</Divider>
    <Table stripe border :columns="columns" :data="listData.data">
    </Table>
    <Spin size="large" fix v-if="load"></Spin>
    <Row v-if="listData.last_page > 1" style="text-align: center;margin-top: 20px">
      <Page @on-change="loadData" :total="listData.total" :page-size="listData.per_page"
            :current="listData.current_page" size="small"/>
    </Row>
  </div>
</template>

<script>
import api from './api'

export default {
  data () {
    return {
      formData: {},
      load: false,
      listData: {},
      columns: [
        {
          title: 'id',
          align: 'center',
          key: 'id'
        },
        {
          title: '标题',
          align: 'center',
          key: 'title'
        },
        {
          title: '创建时间',
          align: 'center',
          key: 'create_time'
        },
        {
          title: '状态',
          key: 'status',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.status === 0 ? '锁定' : '可用')
          }
        },
        {
          title: 'Action',
          width: 200,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'success',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.add(params.row.id)
                  }
                }
              }, '编辑'),
              h('Button', {
                props: {
                  type: 'error',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.del(params.row)
                  }
                }
              }, '删除')
            ])
          }
        }
      ]
    }
  },
  activated () {
    this.loadData()
  },
  methods: {
    loadData (page) {
      if (page) {
        this.formData.jump_page = page
      }
      this.formData.type = this.type
      this.load = true
      api.index(this.formData, res => {
        this.listData = res.data.data
        this.load = false
      }, () => {
        this.load = false
      })
    },
    add (id) {
      this.$router.push({
        name: 'consultEdit',
        query: {
          id: id
        }
      })
    },
    ref () {
      this.formData = {}
      this.loadData(1)
    },
    del (item) {
      this.$Modal.confirm({
        title: '确定删除 ' + item.title + ' 吗?',
        okText: '删除',
        cancelText: '取消',
        loading: true,
        onOk: () => {
          api.delete({ id: item.id }, () => {
            this.$Modal.remove()
            this.$Message.success('删除成功')
            this.loadData()
          },
          () => {
            this.$Modal.remove()
          })
        }
      })
    }
  }
}
</script>
